const testGrid = (state = {}, action) => {
  switch (action.type) {
    case 'ADD_CURRENT_PRODUCT':
      return {
        ...state,
        currentProduct: {
          ...action.product
        }
      }
    case 'ADD_CURRENT_DEPLOYMENT':
      return {
        ...state,
        currentDeployment: {
          ...action.deployment
        }
      }
    case 'ADD_CURRENT_INFRA':
      return {
        ...state,
        currentInfra: {
          ...action.infra
        }
      }
    case 'ADD_CURRENT_SCENARIO':
      return {
        ...state,
        currentScenario: {
          ...action.scenario
        }
      }
    default:
      return state
  }
}

export default {
  reducer: testGrid
}


